html,body{margin:0 auto;font-family:"Fira Sans"}


.description{display:flex;flex-direction:column;align-items:center;justify-content:center;height:100vh;background:url(https://images.unsplash.com/photo-1501139083538-0139583c060f?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80) no-repeat center center fixed;background-size:cover;opacity:0.5;top:0;left:0;bottom:0;right:0;z-index:-1}.title,.description-text{text-align:center;color:white;font-size:5em;text-transform:uppercase}.description-text{font-size:3em;color:red}

.pomodoro{display:flex;flex-direction:column;align-items:center;justify-content:center;background-color:#131212;height:100vh}.pomo-header{font-size:3em;color:white;text-transform:uppercase}

a{padding:25px 30px;color:#6ff403;text-decoration:none;text-transform:uppercase;transition:0.5s;letter-spacing:4px;overflow:hidden}a:hover{background:#6ff403;color:#050801;box-shadow:0 0 5px #6ff403, 0 0 25px #6ff403, 0 0 50px #6ff403, 0 0 200px #6ff403;-webkit-box-reflect:below 1px linear-gradient(transparent, rgba(0,0,0,0.333333))}a:nth-child(1){-webkit-filter:hue-rotate(270deg);filter:hue-rotate(270deg)}a:nth-child(2){-webkit-filter:hue-rotate(110deg);filter:hue-rotate(110deg)}a span{position:absolute;display:block}a span:nth-child(1){top:0;left:0;width:100%;height:2px;background:linear-gradient(90deg, transparent, #6ff403);-webkit-animation:animate1 1s linear infinite;animation:animate1 1s linear infinite}@-webkit-keyframes animate1{0%{left:-100%}50%,100%{left:100%}}@keyframes animate1{0%{left:-100%}50%,100%{left:100%}}a span:nth-child(2){top:-100%;right:0;width:2px;height:100%;background:linear-gradient(180deg, transparent, #6ff403);-webkit-animation:animate2 1s linear infinite;animation:animate2 1s linear infinite;-webkit-animation-delay:0.25s;animation-delay:0.25s}@-webkit-keyframes animate2{0%{top:-100%}50%,100%{top:100%}}@keyframes animate2{0%{top:-100%}50%,100%{top:100%}}a span:nth-child(3){bottom:0;right:0;width:100%;height:2px;background:linear-gradient(270deg, transparent, #6ff403);-webkit-animation:animate3 1s linear infinite;animation:animate3 1s linear infinite;-webkit-animation-delay:0.5s;animation-delay:0.5s}@-webkit-keyframes animate3{0%{right:-100%}50%,100%{right:100%}}@keyframes animate3{0%{right:-100%}50%,100%{right:100%}}a span:nth-child(4){bottom:-100%;left:0;width:2px;height:100%;background:linear-gradient(360deg, transparent, #6ff403);-webkit-animation:animate4 1s linear infinite;animation:animate4 1s linear infinite;-webkit-animation-delay:0.75s;animation-delay:0.75s}@-webkit-keyframes animate4{0%{bottom:-100%}50%,100%{bottom:100%}}@keyframes animate4{0%{bottom:-100%}50%,100%{bottom:100%}}.link-pointer{cursor:pointer}

.progress{display:flex;flex-direction:column;align-items:center}.adjust{margin-top:50px;width:200px}.closer{margin-top:2em;margin-bottom:0;text-align:center}

h1{text-align:center;text-transform:uppercase;color:white;font-size:3rem}input{width:6rem;font-size:3rem}.timer{display:flex;flex-direction:column;margin-top:3%}

